
.content-top-tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  padding: 1rem 0rem 0.7rem 0rem;
  color: #666;
  position: relative;
  background-color: #fff;
}
.content-top-tab:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.content-top-tab > ul {
  width: 100%;
  text-align: center;
}
.content-top-tab > ul > .activity_show {
  color: #00c1b0;
}
.content-top-tab > ul > li {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 0.5rem;
  width: 23%;
}
.contwarrp {
  position: absolute;
  top: 2.9rem;
  bottom: 2rem;
  overflow: hidden;
}
.contwarrp .contentshow {
  width: 100%;
  overflow: hidden;
}
.contwarrp .contentshow .contli {
  padding-bottom: 0.1rem;
  margin-bottom: 0.3rem;
  position: relative;
}
.contwarrp .contentshow .contli:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.contwarrp .contentshow .contli > span {
  display: block;
  font-size: 0.5rem;
  color: #999;
  width: 90%;
  margin: 0 auto;
  margin-bottom: 0.3rem;
}
.contwarrp .contentshow .contli > span > .conttit {
  color: #000;
}


