
.bottom-tab {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  background-color: #fff;
}
.bottom-tab > ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  text-align: center;
}
.bottom-tab > ul > li {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 0.5rem;
  color: #666;
  cursor: pointer;
}
.bottom-tab > ul > li > a.active {
  color: #00c1b0;
}


