
.addorder {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 2rem;
  z-index: 60;
  width: 100%;
  background: #fff;
}
.addorder.move-enter-active,
.addorder.move-leave-active {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.addorder.move-enter,
.addorder.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.addorder .detailtit {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  background-color: #00c1b0;
  color: #fff;
  text-align: center;
  padding: 0.9rem 0.6rem 0.9rem 0.3rem;
  font-size: 0.7rem;
}
.addorder .detailtit .arrow {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  cursor: pointer;
}
.addorder .detailtit .titlefont {
  -webkit-box-flex: 7;
      -ms-flex: 7;
          flex: 7;
  cursor: pointer;
}
.addorder .detailtit .resolve {
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
  text-align: center;
  cursor: pointer;
}
.addorder .infolist {
  width: 95%;
  margin: 0 auto;
  background-color: #fff;
  color: #666;
  text-align: left;
  font-size: 0.7rem;
  margin-top: 0.5rem;
}
.addorder .infolist .paidaninfo {
  width: 100%;
}
.addorder .infolist .paidaninfo .paidande span {
  display: block;
  position: relative;
  height: 2rem;
  line-height: 2rem;
  font-size: 0.5rem;
}
.addorder .infolist .paidaninfo .paidande span:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.addorder .infolist .paidaninfo .paidande span > input {
  width: 80%;
}
.addorder .infolist .paidaninfo .paidande span > input:focus {
  outline: none;
}


